@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@200..800&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
/*********************************/
/*             General           */
/*===============================*/
html {
  @apply scroll-smooth;
}

body {
  @apply text-[15px] text-slate-900;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  @apply font-roboto;
}

p {
  @apply leading-relaxed;
}

input[type=number] {
  -moz-appearance: textfield;
}
input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/*********************************/
/*             Menu              */
/*===============================*/
.navbar {
  @apply fixed top-0 left-0 right-0 z-999 bg-transparent py-3 transition-all duration-500 font-roboto;
}
.navbar .navigation {
  @apply w-full m-0;
}
.navbar .navigation .navbar-nav {
  @apply mt-4 flex flex-row overflow-y-scroll h-24;
}
.navbar .navigation .navbar-nav .nav-link {
  @apply block p-3 text-[14px] text-slate-900 tracking-wider hover:text-red-500 dark:text-white/60 dark:hover:text-white font-semibold;
}
.navbar .navigation .navbar-nav .active .nav-link {
  @apply text-red-500 dark:text-white;
}
.navbar .navigation.ms-auto .navbar-nav li:last-child .nav-link {
  @apply pe-0;
}
.navbar .navigation.me-auto .navbar-nav li:first-child .nav-link {
  @apply ps-0;
}
.navbar .navbar-brand {
  @apply p-0;
}
.navbar .navbar-brand .l-dark,
.navbar .navbar-brand .logo-dark-mode {
  @apply hidden;
}
.navbar .navbar-brand .l-light,
.navbar .navbar-brand .logo-light-mode {
  @apply inline-block;
}
@media (max-width: 991.98px) {
  .navbar .navbar-brand .l-dark {
    @apply inline-block !important;
  }
  .navbar .navbar-brand .l-light {
    @apply hidden !important;
  }
}
.navbar .menu-social .login-btn-primary {
  @apply hidden;
}
.navbar .menu-social .login-btn-light {
  @apply inline-block;
}
@media (max-width: 991.98px) {
  .navbar .menu-social .login-btn-primary {
    @apply inline-block !important;
  }
  .navbar .menu-social .login-btn-light {
    @apply hidden !important;
  }
}
.navbar.is-sticky {
  @apply bg-white dark:bg-slate-900 shadow dark:shadow-gray-800;
}
.navbar.is-sticky .navbar-brand .l-dark {
  @apply inline-block;
}
.navbar.is-sticky .navbar-brand .l-light {
  @apply hidden;
}
.navbar.is-sticky .menu-social .login-btn-primary {
  @apply inline-block;
}
.navbar.is-sticky .menu-social .login-btn-light {
  @apply hidden;
}

@media (min-width: 992px) {
  .navbar {
    @apply py-3;
  }
  .navbar .navigation {
    @apply flex w-auto;
  }
  .navbar .navigation .navbar-nav {
    @apply h-auto mt-0 overflow-visible;
  }
  .navbar .navigation .navbar-nav .nav-link {
    @apply border-0 hover:bg-transparent;
  }
  .navbar .navigation .navbar-nav.nav-light .nav-link {
    @apply block p-3 text-white/60 hover:text-white;
  }
  .navbar .navigation .navbar-nav.nav-light .active .nav-link {
    @apply text-white;
  }
  .navbar .nav-icons .collapse-btn {
    @apply hidden;
  }
  .navbar.is-sticky {
    @apply py-3;
  }
  .navbar.is-sticky .navbar-nav .nav-link {
    @apply text-slate-900 dark:text-white/60;
  }
  .navbar.is-sticky .navbar-nav .active .nav-link {
    @apply text-red-500;
  }
  .navbar.is-sticky .navbar-nav.nav-light .nav-link {
    @apply text-slate-900 hover:text-red-500 dark:text-white/60 dark:hover:text-white;
  }
  .navbar.is-sticky .navbar-nav.nav-light .active .nav-link {
    @apply text-red-500 dark:text-white;
  }
}
@media (max-width: 991px) {
  .navbar {
    @apply bg-white shadow dark:shadow-gray-800 dark:bg-slate-900 !important;
  }
  .navbar .navigation .navbar-nav {
    @apply block h-full max-h-80;
  }
  .navbar .navigation .navbar-nav .nav-link {
    @apply text-slate-900;
  }
  .navbar .navigation .navbar-nav .active .nav-link {
    @apply text-red-500;
  }
  .navbar .navigation .navbar-nav .menu-social {
    @apply mb-5 !important;
  }
}
/*********************************/
/*             Helper            */
/*===============================*/
.container,
.contacinr-fluid {
  @apply relative;
}

.text-type-element {
  @apply z-2 after:content-[""] after:absolute after:h-1 after:w-auto after:bg-orange-300 after:end-0 after:bottom-0 after:start-0 after:z-0 after:rounded-md after:transition-all after:duration-300 after:ease-in-out;
}

/*********************************/
/*         Tobii Lightbox        */
/*===============================*/
/* Tobii Lighbox */
.tobii > button.tobii__close,
.tobii > button.tobii__prev,
.tobii > button.tobii__next {
  @apply focus-visible:outline-none !important;
}
.tobii > button.tobii__close svg,
.tobii > button.tobii__prev svg,
.tobii > button.tobii__next svg {
  @apply h-9 w-auto;
}

.tobii__counter {
  @apply text-base;
}

.tobii-zoom {
  @apply block;
}

.tobii-zoom__icon {
  @apply hidden;
}

#grid {
  @apply p-0;
}

/*****************/
/*    Shapes     */
/*****************/
/* Shapes */
.shape {
  @apply absolute start-0 end-0 -bottom-px;
}
.shape > svg {
  @apply w-full h-auto;
  transform: scale(2);
  transform-origin: theme("transformOrigin.top") theme("transformOrigin.center");
}

/*****************/
/*  back-to-top  */
/*****************/
.back-to-top {
  @apply hidden;
}

/*****************/
/*    Switcher   */
/*****************/
.label .ball {
  transition: transform 0.2s linear;
  @apply translate-x-0;
}

.checkbox:checked + .label .ball {
  @apply translate-x-6;
}

/*****************/
/*   Preloader   */
/*****************/
#preloader {
  background-image: linear-gradient(45deg, #ffffff, #ffffff);
  z-index: 99999;
  @apply fixed inset-0;
}
#preloader #status {
  @apply absolute start-0 end-0 top-1/2 -translate-y-1/2;
}
#preloader #status .spinner {
  @apply w-10 h-10 relative my-[100px] mx-auto;
}
#preloader #status .spinner .double-bounce1, #preloader #status .spinner .double-bounce2 {
  @apply w-full h-full rounded-full bg-red-500/60 absolute top-0 start-0;
  animation: sk-bounce 2s infinite ease-in-out;
}
#preloader #status .spinner .double-bounce2 {
  animation-delay: -1s;
}

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
/*****************/
/*     Mover     */
/*****************/
.mover {
  animation: mover 1.5s infinite alternate;
}

@keyframes mover {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(10px);
  }
}
@media (max-width: 640px) {
  [data-type] iframe, [data-type] video {
    width: 500px;
    height: 300px;
  }
}
@media (max-width: 425px) {
  [data-type] iframe, [data-type] video {
    width: 360px;
    height: 260px;
  }
}
@media (max-width: 375px) {
  [data-type] iframe, [data-type] video {
    width: 100%;
    height: auto;
  }
}
.lawyer-wrapper {
  overflow: hidden;
}
.lawyer-wrapper:before {
  content: "";
  position: absolute;
  bottom: 25rem;
  right: 13rem;
  width: 50rem;
  height: 25rem;
  transform: rotate(100deg);
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.3), transparent);
  z-index: 1;
}
@media (min-width: 769px) and (max-width: 1024px) {
  .lawyer-wrapper:before {
    right: -10rem;
  }
}
@media (min-width: 576px) and (max-width: 768px) {
  .lawyer-wrapper:before {
    right: -15rem;
    height: 20rem;
    bottom: 25rem;
  }
}
@media (max-width: 575px) {
  .lawyer-wrapper:before {
    right: -15rem;
    height: 20rem;
    bottom: 35rem;
  }
}

/*********************************/
/*             Contact           */
/*===============================*/
/* Validation */
.error {
  @apply my-2 mx-0 hidden text-red-500;
}

#ajaxsuccess {
  @apply text-base w-full hidden clear-both my-2 mx-0;
}

.error_message,
#success_page {
  @apply p-2.5 mb-5 text-center rounded-md;
}

.error_message {
  @apply bg-red-500/5 text-red-500;
}

.contact-loader {
  @apply hidden;
}

#success_page {
  @apply bg-emerald-600/5 text-emerald-600;
}

/*****************/
/*  Testimonial  */
/*****************/
.tns-nav {
  @apply text-center mt-3;
}
.tns-nav button {
  @apply rounded-full bg-black/20 dark:bg-white/20 transition-all duration-500 ease-in-out border-2 border-white dark:border-slate-900 m-1 p-[2px];
}
.tns-nav button.tns-nav-active {
  @apply bg-red-500 border-red-500;
}

.tns-controls button[data-controls=prev],
.tns-controls button[data-controls=next] {
  @apply absolute top-1/2 -translate-y-1/2 text-base w-8 h-8 rounded-full bg-white dark:bg-slate-900 text-slate-900 dark:text-white border-none z-1 shadow-lg shadow-slate-200 dark:shadow-slate-700;
}
.tns-controls button[data-controls=prev]:hover,
.tns-controls button[data-controls=next]:hover {
  @apply bg-red-500 text-white;
}
.tns-controls button[data-controls=prev] {
  @apply start-0 md:-start-2;
}
.tns-controls button[data-controls=next] {
  @apply end-0 md:-end-2;
}

html[dir=rtl] .tns-inner, html[dir=rtl] .tns-nav, html[dir=rtl] .slider, html[dir=rtl] .slider2 {
  direction: ltr !important;
}
html[dir=rtl] .tns-visually-hidden {
  left: 0 !important;
  visibility: hidden;
}